<template>
    <div class="phone">
        <div class="main">
            <h3></h3>
            <p class="tip">{{tip}}</p>
            <p class="phoneNum">
                <van-field v-model="phoneNum" placeholder="" @focus="onFocus">
                    <van-icon slot="left-icon" name="http://tdream.antit.top/image/tele.png"></van-icon>
                </van-field>
            </p>
        </div>
        <van-number-keyboard
            :show="show"
            theme="custom"
            extra-key="."
            close-button-text=""
            @input="onInput"
            @delete="onDelete"
        />
    </div>
</template>

<script>
export default {
    name:'phone',
    data () {
        return {
            show:true,//,
            phoneNum:'',
            tip:''
        }
    },
    mounted(){
        
    },
    methods:{
        onInput(val){
            console.log(val)
            this.phoneNum += val;
        },
        onDelete(){
            this.phoneNum = this.phoneNum.slice(0, this.phoneNum.length - 1);
        },
        onFocus(){
            document.activeElement.blur();
        }
    }
}
</script>

